import { DotsThreeVertical } from '@styled-icons/entypo'
import { useEffect, useRef } from 'react'
import NavMenu from '../NavMenu/NavMenu'

function HeaderMenu(props) {
	const ref = useRef()

	useEffect(() => {
		if (props.isNavOpen) {
			props.setInfoOpen(false)
			return document
				.querySelector('body')
				.classList.add('overflow-hidden')
		}
		return document
			.querySelector('body')
			.classList.remove('overflow-hidden')
	}, [props.isNavOpen])

	useEffect(() => {
		const checkIfClickedOutside = e => {
			// If the menu is open and the clicked target is not within the menu,
			// then close the menu
			if (
				props.isNavOpen &&
				ref.current &&
				!ref.current.contains(e.target)
			) {
				props.setNavOpen(false)
			}
		}

		document.addEventListener('click', checkIfClickedOutside)

		return () => {
			// Cleanup the event listener
			document.removeEventListener('click', checkIfClickedOutside)
		}
	}, [props.isNavOpen])

	return (
		<div ref={ref}>
			<div onClick={props.onDotsClick}>
				<DotsThreeVertical
					className={`w-[18px] h-[18px] fill-dark-100 transition duration-300 cursor-pointer ${
						props.isNavOpen && '-rotate-90'
					}`}
				/>
			</div>
			<NavMenu
				animate={true}
				isOpen={props.isNavOpen}
				className='fixed right-0 bottom-0 top-[35px] lg:top-[45px] w-[108px] bg-white dark:bg-dark-300 z-0 drop-shadow-2xl'
			/>
		</div>
	)
}

export default HeaderMenu
